import React from 'react'
import './ContactMe.css'
import ContactItem from '../contactitem/ContactItem';
import gmailLogo from '../../assets/gmail.svg';
import linkedinLogo from '../../assets/linkedin.svg';
import githubLogo from '../../assets/github.svg';

function ContactMe() {

    return (
        <div className="ContactMe">
            <p className="contactMeTitle">CONTACT ME</p>
            <div className="ContactItemContainer">
                <ContactItem link="mailto:ismailshaikh31097@gmail.com" icon={gmailLogo} text="ismailshaikh31097@gmail.com" />
                <ContactItem link="https://www.linkedin.com/in/ismail-creatvt" icon={linkedinLogo} text="linkedin.com/in/ismail-creatvt" />
                <ContactItem link="https://www.github.com/ismail-creatvt" icon={githubLogo} text="github.com/ismail-creatvt" />
            </div>
        </div>
    )
}

export default ContactMe
