import React from 'react';
import './SkillItem.css';

function SkillItem({ icon, title, description, experience }) {
    return (
        <div className="SkillItem">
            <div className="SkillItem__image">
                <img src={icon} alt="Logo" />
            </div>
            <div className="SkillItem__content">
                <p className="SkillItem__title">{title}</p>
                <p className="SkillItem__description">{description}</p>
                <p className="SkillItem__experience">{experience}</p>
            </div>
        </div>
    )
}

export default SkillItem
