import React from 'react'
import './ViewMyResume.css'
import arrowForward from '../../assets/arrow_forward_white_24dp.svg';

function ViewMyResume() {
    return (
        <a href="https://ismailportfolio.github.io/Ismail Shaikh - Resume (2020).pdf" target="_blank"><button className="ViewMyResume">VIEW MY RESUME <img src={arrowForward} alt="forward" /></button></a>
    )
}

export default ViewMyResume